<template lang="html">
  <div class="search" ref="search">
    <div class="search-box-wrapper">
      <search-box ref="searchBox" @change="queryChange"></search-box>
    </div>
    <div class="shortcut-wrapper" v-show="!query">
      <scroller :refreshDelay="refreshDelay" class="shortcut" ref="scroller" :data="shortcut">
        <div class="hot-key">
          <h1 class="title">热门搜索</h1>
          <div class="loading-wrapper">
            <loading v-show="hotkey.length < 1" :scale="1.5"></loading>
          </div>
          <ul>
            <li class="item" v-for="item in hotkey" @click="setQuery(item.k.trim())">
              <span>{{ item.k.trim() }}</span>
            </li>
          </ul>
        </div>
        <search-list
        :search="searchHistory"
        @select="setQuery"
        @del="decacheSearch"
        @delAll="clearSearchHistory"
        ></search-list>
      </scroller>
    </div>
    <div class="search-result" v-show="query" ref="searchResult" @touchstart="blurInput">
      <suggest ref="suggest" :query="query" @select="saveSearch"></suggest>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
  import 'vue-awesome/icons/trash-o'
  import { ERR_OK } from 'api/config'
  import { catchErrors } from 'api/handler'
  import { getHotkey } from 'api/search'
  import Loading from 'components/base/loading/loading3'
  import PlayerMixin from '@/mixins/player-mixin'
  import SearchMixin from '@/mixins/search-mixin'
  import { miniPlayerHeight } from 'assets/js/config'

  export default {
    name: 'search',
    components: { Loading },
    mixins: [PlayerMixin, SearchMixin],
    data () {
      return {
        hotkey: []
      }
    },
    computed: {
      shortcut () {
        return [
          ...this.hotkey,
          ...this.searchHistory
        ]
      }
    },
    created () {
      this._getHotkey()
    },
    methods: {
      _getHotkey: catchErrors(async function () {
        const hotkeyRes = await getHotkey()
        if (hotkeyRes && hotkeyRes.code === ERR_OK) {
          this.hotkey = hotkeyRes.data.hotkey.slice(0, 10)
        }
      }),
      handlePlaylist (playlist) {
        const bottom = playlist.length > 0 ? miniPlayerHeight : 0
        this.$refs.search.style.bottom = bottom
        this.$refs.suggest.refresh()
        this.$refs.scroller.refresh()
      }
    }
  }
</script>

<style scoped lang="less">
  @import "~assets/less/variable";
  @import "~assets/less/mixin";

  .loading-wrapper {
    text-align: center;
  }

  .search {
    position: absolute;
    top: 88px;
    bottom: 0;
    width: 100%;
    overflow: hidden;
    .search-box-wrapper {
      margin: 10px 20px 20px 20px;
    }
    .shortcut-wrapper {
      position: absolute;
      top: 70px;
      bottom: 10px;
      width: 100%;
      overflow: hidden;
      .shortcut {
        height: 100%;
        overflow: hidden;
        position: relative;
        .hot-key {
          margin: 5px 20px;
          .title {
            margin-bottom: 20px;
            font-size: @font-size-medium;
            color: @color-text-l;
          }
          .item {
            display: inline-block;
            padding: 5px 10px;
            margin: 0 15px 10px 0;
            border-radius: 6px;
            background: @color-highlight-background;
            font-size: @font-size-medium;
            color: @color-text-d;
          }
        }
      }
    }
    .search-result {
      position: absolute;
      width: 100%;
      top: 70px;
      bottom: 0;
    }
  }
</style>



